.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid $colorBlue;
  border-radius: 8px;

  font-weight: 700;
  font-size: 16px;
  line-height: 14px;

  text-align: center;

  color: $colorBlue;
  height: 56px;
  text-decoration: none;

  cursor: pointer;

  &--primary {
    color: #fff;
    background-color: $colorBlue;
    box-shadow: 0.1px 6px 23px rgba(0, 117, 255, 0.46);
    width: 100%;
  }

  @include onDesktop {
    max-width: 150px;
    margin: 0 auto;
  }

  &:hover {
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0.1px 13px 27px rgba(0, 83, 181, 0.46);
    color: #fff;
  }

  &:active {
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0.1px 8px 14px rgba(0, 83, 181, 0.48);
    color: #fff;
  }
}
